import { createStore } from 'vuex';
import axios from 'axios';

const GOOGLE_SHEETS_API_KEY = process.env.VUE_APP_GOOGLE_SHEETS_API_KEY;
const SPREADSHEET_ID = '1Z6wmI5LrukcW4rmHh3XTq-4GZvPoWRVJ_f1Q_ND2XPk';


const debugPlugin = (store) => {
    store.subscribe((mutation, state) => {
        console.log('🧟‍♂️ MUTATION:', mutation.type);
        console.log('💌 Payload:', mutation.payload);
        console.log('🎁 Current State:', state);
    });
};

const store = createStore({
    state: {
        cards: [],                 // Tarot cards data
        selectedCard: null,        // Selected card for modal
        isModalVisible: false,     // Modal visibility state
        isLoading: false,          // Loading state for API calls
        enableEditMeanings: process.env.VUE_APP_ENABLE_EDIT_MEANINGS === 'true',  // Set based on env variable
    },

    mutations: {
        SET_CARDS(state, cards) {
            state.cards = cards;
        },
        SET_SELECTED_CARD(state, card) {
            state.selectedCard = card;
        },
        TOGGLE_MODAL(state, isVisible) {
            state.isModalVisible = isVisible;
        },
        TOGGLE_MEANINGS_EDIT(state, editEnabled) {
            state.enableEditMeanings = editEnabled;
        },
        SET_LOADING(state, isLoading) {
            state.isLoading = isLoading;
        },
        //TODO I have a new meaning model.
        UPDATE_CARD_MEANING(state, { cardId, cellId, content }) {
            const card = state.cards.find(c => c.id === cardId);
            const meaning = card.meanings.find(meaning => meaning.cellId === cellId)
            if (meaning) {
                meaning.value = content;
            }
        }
    },

    actions: {
        async fetchTarotCards({ commit }) {
            commit('SET_LOADING', true);
            const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPREADSHEET_ID}/values/Database?key=${GOOGLE_SHEETS_API_KEY}`;

            try {
                //TODO the fetching logic is more complex now
                const response = await axios.get(url);
                const rows = response.data.values;
                const headers = rows[0];

                console.warn('💯 My headers', JSON.stringify(headers));

                const filteredRows = rows.slice(1).filter(row => row && row[0]);

                const cards = filteredRows.slice(1).map((row, index) => {
                    const card = {
                        id: row[0],
                        alias: row[1],
                        name: row[2],
                        alt_name: row[3],
                        searchable_name: row[1] + '-' + row[2] + '-' + row[3],
                        image: row[6],
                        keywords: row[12],//new
                        lucky: row[13],
                        meanings: []
                    }

                    for (let i = 13; i < headers.length; i++) {
                        const header = headers[i];  // Get the corresponding header for this column
                        let content = row[i];     // Get the content for this card in the current column


                        // If the header or content is missing, break the loop
                        if (!header) break;

                        // Add to meanings if valid
                        card.meanings.push({
                            key: header,
                            value: content,
                            cellId: `${indexToLetter(i)}${index + 2}`,
                        });
                    }
                    return card;
                });

                commit('SET_CARDS', cards);
            } catch (error) {
                console.error("Failed to fetch Tarot cards:", error);
            } finally {
                commit('SET_LOADING', false);
            }
        },

        selectCard({ commit }, card) {
            commit('SET_SELECTED_CARD', card);
            commit('TOGGLE_MODAL', true);  // Open modal
        },

        async saveCardMeaning({ commit }, { cardId, cellId, content }) {
            try {
                console.log('trying to save', cardId, cellId, content);
                const url = `https://script.google.com/macros/s/AKfycbzBNFcChAOjNXvHZGbknF-r-vo1xcHeueECHPbMR1uDf7XrbxaYOMpUxElb3jlDESXN/exec`;
                await axios.post(url, {
                    range: cellId,
                    value: content  // Update the meaning in Google Sheets
                }, {
                    headers: {
                        "Content-Type": "text/plain;charset=utf-8",
                    }
                });
                commit('UPDATE_CARD_MEANING', { cardId, cellId, content });  // Update Vuex state
            } catch (error) {
                console.error("Error saving meaning:", error);
            }
        },
        closeModal({ commit }) {
            commit('TOGGLE_MODAL', false);
        }
    },

    getters: {
        getCardById: (state) => (cardId) => {
            return state.cards.find(card => card.id === cardId);
        },
        filteredCards: (state) => (query) => {
            if (!query) return state.cards;
            return state.cards.filter(card => card.name.toLowerCase().includes(query.toLowerCase()));
        }
    },

    plugins: [debugPlugin],  // Register the plugin here
});

// helper functions
function indexToLetter(index) {
    const lettersInSpredsheet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    return lettersInSpredsheet.charAt(index);
}

export default store;
