<template>
    <div>
        <v-card :key="card.name" v-for="card in filteredCards" @click="selectCard(card)" class="list-item">
            <v-row>
                <v-col key="image" cols="4">
                    <v-img :src="card.image" height="200px"></v-img>
                </v-col>
                <v-col key="content" cols="8">
                    <v-card-title>{{ card.name }}</v-card-title>
                    <v-card-subtitle>{{ card.alt_name }}</v-card-subtitle>
                    <v-card-text>{{ card.lucky }}</v-card-text>
                </v-col>

            </v-row>
        </v-card>
    </div>
</template>
  
<script>
import Fuse from 'fuse.js';
import { mapActions } from 'vuex';


export default {
    props: {
        cards: Array,
        searchQuery: String
    },
    data() {
        return {
            fuse: null
        };
    },
    watch: {
        searchQuery() {
            this.updateSearch();
        },
        cards(newCards) {
            if (newCards && newCards.length > 0) {
                const options = {
                    keys: ['searchable_name'],
                    threshold: 0.3  // Adjust this to make the search more or less fuzzy
                };
                this.fuse = new Fuse(newCards, options);
            }
        }
    },
    mounted() {
        if (this.cards && this.cards.length > 0) {
            const options = {
                keys: ['searchable_name'],
                threshold: 0.3  // Adjust this to make the search more or less fuzzy
            };
            this.fuse = new Fuse(this.cards, options);
        }
    },
    computed: {
        filteredCards() {
            if (!this.searchQuery) {
                return this.cards;
            }
            const searchRes = this.fuse.search(this.searchQuery);
            console.warn('HELLO', searchRes);
            return searchRes.map(result => result.item);
        }
    },
    methods: {
        ...mapActions(['selectCard']),
        updateSearch() {
            console.log('myquery', this.searchQuery);

        }
    }
};
</script>
  
