<template>
  <v-bottom-navigation v-model="activeView" grow background-color="primary" dark>
    <v-btn :value="'cardList'" @click="changeView('cardList')" icon>
      <v-icon>mdi-cards</v-icon>
    </v-btn>

    <v-btn :value="'sortCards'" @click="changeView('sortCards')" icon>
      <v-icon>mdi-sort-variant</v-icon>
    </v-btn>

    <v-btn :value="'settings'" @click="changeView('settings')" icon>
      <v-icon>mdi-cog</v-icon>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
export default {
  props: {
    currentView: String
  },
  data() {
    return {
      activeView: this.currentView || 'cardList'
    };
  },
  methods: {
    changeView(view) {
      this.$emit('view-changed', view);
    }
  }
};
</script>

