<template>
  <div>

    <v-app>
      <v-app-bar title="Tarot - Forbidden Memories" color="primary" dark></v-app-bar>
      <v-main>
        <v-container>
          <div v-if="currentView === 'cardList'">
            <v-container class="sticky-search">
              <SearchBox @search="updateSearch" />
            </v-container>
            <v-container class="scrollable-content">
              <CardList :cards="cards" :searchQuery="searchQuery" @card-selected="selectCard" />
              <CardModal :card="selectedCard" :isVisible="isModalVisible" @close="closeModal" />  
            </v-container>
            
          </div>

          <div v-if="currentView === 'sortCards'">
            <!-- Placeholder for future sorted card view -->
            <h2>Sort Cards View (To be implemented)</h2>
          </div>

          <div v-if="currentView === 'settings'">
            <!-- Placeholder for settings view -->
            <h2>Settings View (To be implemented)</h2>
            <v-switch v-model="isDark" :label="`Dark Theme: ${isDark ? 'On' : 'Off'}`" inset></v-switch>
          </div>
        </v-container>
        <GlobalFooter :currentView="currentView" @view-changed="updateView"></GlobalFooter>
      </v-main>
    </v-app>

    <!-- Conditionally render different views based on the currentView -->

  </div>
</template>

<script>
import SearchBox from './components/ui/SearchBox.vue';
import CardList from './components/ui/CardList.vue';
import CardModal from './components/ui/CardModal.vue';
import GlobalFooter from './components/ui/GlobalFooter.vue';

// this is probably going to get deprecated
import { ref, onMounted, watch } from 'vue'
import { useTheme } from 'vuetify'
import { mapState, mapActions, mapGetters } from 'vuex';

export default {
  setup() {
    // Reactive boolean to track the dark theme state
    const isDark = ref(localStorage.getItem('isDark') === 'true' || false)

    // Access Vuetify's theme object using Composition API
    const theme = useTheme()

    // Watch for changes to isDark and update the theme
    const updateTheme = (val) => {
      theme.global.name.value = val ? 'dark' : 'light'
      localStorage.setItem('isDark', val) // Persist theme preference
    }

    watch(isDark, (newValue) => {
      updateTheme(newValue)
    })

    // On mount, set the initial theme based on localStorage
    onMounted(() => {
      updateTheme(isDark.value)
    })

    return {
      isDark,
      updateTheme
    }
  },
  data() {
    return {
      searchQuery: '',
      currentView: 'cardList', // Default view
    };
  },
  computed: {
    themeMode() {
      return this.isDark ? 'dark' : 'light';
    },
    filteredCards() {
      return this.$store.getters.filteredCards(this.searchQuery);
    },
    ...mapState(['isModalVisible', 'selectedCard', 'cards']),
    ...mapGetters(['filteredCards'])
  },
  mounted() {
    this.fetchTarotCards();
  },

  methods: {
    updateSearch(query) {
      this.searchQuery = query;
    },

    handleTarotData(cards) {
      this.cards = cards;  // Update cards with data fetched by the provider
      console.warn('MY CARDS', cards);
    },
    updateView(view) {
      this.currentView = view;
    },
    ...mapActions(['fetchTarotCards', 'selectCard', 'closeModal']),
  },
  components: {
    SearchBox,
    CardList,
    CardModal,
    GlobalFooter
  }
};
</script>

