<template>
    <v-dialog fullscreen persistent overlay scrollable
      v-model="activator"
      transition="dialog-bottom-transition"

    >
      <v-card class="modal-content desktop-layout">
        <v-toolbar>
          <v-btn
            icon="mdi-close"
            @click="closeModal"
          ></v-btn>

          <v-toolbar-title>Significado - {{ card.name }} </v-toolbar-title>
        </v-toolbar>

        <v-row class="card-container-general" no-gutters>
          <v-col key="image" cols="12" sm="4">
            <div class="modal-card-image">
              <img :src="card.image" alt="Card Image" />
            </div>
          </v-col>
          <v-col key="content" cols="12" sm="8" class="card-container-content">
            <v-card class="list-item">
              <v-card-title>{{ card.name }}</v-card-title>
              <v-card-subtitle>{{ card.alt_name }}</v-card-subtitle>

              <v-card-text>
                {{ card.keywords }}
              </v-card-text>
            </v-card>
            <v-card v-for="meaning in card.meanings" :key="meaning.key" class="list-item">
              <v-card-title class="meaning-header">
                {{ meaning.key }}
                <v-btn class="meaning-header-button" v-if="(this.$store.state.enableEditMeanings && !meaning.isEditing)" icon="$edit"
                  @click="editCardMeaning(card, meaning)"></v-btn>
              </v-card-title>
              <div v-if="meaning.isEditing">
                <v-textarea label="Label" variant="solo-filled" v-model="meaning.draftValue"></v-textarea>
                <v-card-actions>
                  <v-btn @click="handleSaveCardMeaning(card, meaning)">Guardar</v-btn>
                  <v-btn @click="handleCancelSaveCardMeaning(card, meaning)">Cancelar</v-btn>
                </v-card-actions>
              </div>
              <div v-else>
                <v-card-text>
                  {{ meaning.value }}
                </v-card-text>
              </div>

            </v-card>
          </v-col>

        </v-row>


      </v-card>
    </v-dialog>
</template>

<script>
import { mapActions } from 'vuex';



export default {
  data() {
    return {
      isEditing: false,
      activator: false

    }
  },
  props: {
    card: Object,  // The card data
    isVisible: Boolean,  // Controls visibility of modal
  },
  computed: {
    layoutClass() {
      return window.innerWidth > 768 ? 'desktop-layout' : 'mobile-layout';
    }
  },
  watch: {
    isVisible(newVal){
      this.activator = newVal;
    }
  },
  methods: {
    closeModal() {
      this.$emit('close');
    },
    handleResize() {
      // Recalculate layout on window resize
      this.layoutClass = window.innerWidth > 768 ? 'desktop-layout' : 'mobile-layout';
    },
    editCardMeaning(card, meaning) {
      meaning.isEditing = true;
      meaning.draftValue = meaning.value;
      console.warn('my card', card);
      console.warn('meaning', meaning);

    },
    async handleSaveCardMeaning(card, meaning) {
      console.warn('my card', card);
      console.warn('meaning', meaning);
      meaning.value = meaning.draftValue;
      meaning.isEditing = false;


      await this.saveCardMeaning({
        cardId: card.id,
        cellId: meaning.cellId,
        content: meaning.value
      });
    },
    handleCancelSaveCardMeaning(card, meaning) {
      meaning.isEditing = false;
    },
    ...mapActions(['saveCardMeaning'])
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
  },
  unmounted() {
    window.removeEventListener('resize', this.handleResize);
  }
};
</script>

